<template>
  <div id="messagePlan">
    <div class="top">
      <img class="img" :src="headerImage" alt @click="chooseHead"/>
      <div class="headerBox" v-if="isChooseHead">
        <h2>选择你的头像</h2>
        <ul id="chooseHeader">
          <li
              class="headerImg"
              :class="{isClick:chooseIndex===index}"
              @click="clickHead(index)"
              v-for="(img,index) in headerImg"
              :style="{backgroundImage:`url(${img.url})`}"
          >
          </li>
        </ul>
        <div class="button">
          <el-button @click="chooseHeaderOver">确定</el-button>
          <el-button @click="closeHeaderBox">取消</el-button>
        </div>

      </div>

      <div class="userName">
        <el-input v-model="name" placeholder="请输入您的称呼"/>
        <p class="position">说：</p>
      </div>
    </div>
    <div class="bottom">
      <el-input
          class="comment"
          type="textarea"
          :rows="7"
          placeholder="在这里输入留言内容"
          v-model="comment"
      />
      <div class="prompt">
        <p v-if="isNull" style="color: red">请输入内容！</p>
        <p v-if="success" style="color: green">留言成功！</p>
      </div>
    </div>
    <el-button @click="sendMessage" class="send">保存留言</el-button>
  </div>
</template>

<script>
  import {getHeaderImg, postSenMag} from "../../../../service/api";
  export default {
    name: "MessagePlan",
    data() {
      return {
        name: "",
        comment: "",
        headerImage: "",//头像图片
        isNull: false,
        success: false,
        isChooseHead: false, //控制头像面板展示
        chooseIndex: -1,
        headerImg: []  //头像资源
      };
    },
    methods: {
      async sendMessage() {
        if (this.comment === "") {
          this.isNull = true;
          return;
        }
        let mag = {
          name: this.name,
          headerimageurl: this.headerImage,
          comment: this.comment,
          date: new Date().getTime()
        };
        this.isNull = false;
        this.success = true;
        this.name = "";
        this.comment = "";
        await postSenMag(mag);
        await this.$store.dispatch("getMassage");
      },
      //选择头像
      chooseHead() {
        if (this.isChooseHead)
          this.isChooseHead = false;
        else this.isChooseHead = true;
      },
      //点击头像
      clickHead(index) {
        this.chooseIndex = index;
      },
      //关闭头像选择框
      closeHeaderBox() {
        this.isChooseHead = false;
        this.chooseIndex = -1;
      },
      //选择头像完毕
      chooseHeaderOver() {
        this.isChooseHead = false;
        this.headerImage = this.headerImg[this.chooseIndex].url;
        this.chooseIndex = -1;
      }
    },
    created() {
      //请求网络数据
      getHeaderImg()
        .then(response => {
          this.headerImg = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
  };
</script>

<style lang="scss" scoped>
  @import "../../../../style/statement";
  #messagePlan {
    width: 80%;
    height: 400px;
    background-color: #fff;
    border-radius: 4px;
    margin: 10px auto;
    padding: 30px;
    box-sizing: border-box;
    box-shadow: 5px 5px 10px 10px rgba(0, 0, 0, 0.2);
    position: relative;
  }
  .top {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
  }
  .img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: blue;
  }
  /*选择头像面板*/
  .headerBox {
    position: absolute;
    border-radius: 10px;
    background-color: white;
    z-index: 10;
    box-shadow: 5px 5px 10px 10px rgba(0, 0, 0, 0.2);
    h2 {
      margin: 10px;
    }
    .button {
      margin: 10px 0 10px 100px;
    }
  }
  #chooseHeader {
    width: 240px;
    height: 160px;
    left: 0;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
    overflow: auto;
    .headerImg {
      width: 80px;
      height: 80px;
      background-color: aliceblue;
      box-sizing: border-box;
      background-size: cover;
    }
    .isClick {
      border: 10px solid aquamarine;
    }
  }
  .userName {
    width: 50%;
    margin-left: 20px;
  }
  .position {
    margin-top: 10px;
  }
  .comment {
    margin-top: 30px;
  }
  .send {
    margin-top: 20px;
    float: right;
  }
  @include styleAllPhone {
    #messagePlan {
      width: 80%;
      height: conversionPX(400);
      margin: conversionPX(10) auto;
      padding: conversionPX(30);
    }
    .top {
      height: conversionPX(80);
    }
    .img {
      width: conversionPX(60);
      height: conversionPX(60);
    }
    .userName {
      width: 65%;
      margin-left: conversionPX(20);
    }
    .position {
      margin-top: conversionPX(10);
    }
    .comment {
      margin-top: conversionPX(30);
    }
    .send {
      margin-top: conversionPX(20);
    }
  }
</style>
